main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
$content-section-padding: 20px;
$news-bg-color: #ffffff;
$contact-us-bg-color: #841983;
$text-color: rgb(0, 0, 0);
$background-color: #ffffff;

.content-section {
  max-width: 100%;
  display: grid;
  align-items: start; /* 顶端对齐 */
  grid-template-columns: 70% 30%; /* 设置列比例 */
  gap: $content-section-padding; /* 增加列间距 */
  padding: $content-section-padding;
  background-color: $background-color;

  .news, .contact-us {
    padding: $content-section-padding;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* 保证内容顶端对齐 */
  }

  .news {
    background-color: $news-bg-color;
    color: rgb(0 , 0 , 0);
  }

  .contact-us {
    background-color: $contact-us-bg-color;
    color: #ffffff;
  }

  @media (max-width: 768px) {
    grid-template-columns: 1fr; /* 变为单列布局 */
    .news, .contact-us {
      width: 100%;
      margin-bottom: $content-section-padding;
    }
  }
}

.image-slider {
  display: flex;
  overflow: hidden;
  gap: 10px;

  .slider-item {
    position: relative;
    width: 300px; /* 每个图片的宽度 */
    height: 200px; /* 每个图片的高度 */
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .info {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 10px;
      background: rgba(0, 0, 0, 0.5);
      color: white;
      transition: all 0.3s ease-in-out;

      .title {
        font-size: 16px;
        margin: 0;
      }

      .conference {
        font-size: 14px;
        margin: 0;
        opacity: 0;
        transform: translateY(20px);
        transition: all 0.3s ease-in-out;
      }
    }

    &:hover .info {
      bottom: 50%; /* 移动到图片中央 */
      background: rgba(0, 0, 0, 0.7);
      transform: translateY(50%);
    }

    &:hover .conference {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

.slider-container {
  position: relative;
  width: 100%;
  max-width: 1000px; /* 你可以根据需要调整宽度 */
  margin: auto;
  overflow: hidden; /* 隐藏溢出的部分 */

  .slider-wrapper {
    display: flex;
    transition: transform 0.5s ease-in-out; /* 动画效果 */
  }

  .slider-item {
    flex: 0 0 33.33%; /* 每个 item 占据三分之一的空间 */
    position: relative;
    overflow: hidden; /* 确保子元素不会溢出 */

    img {
      width: 100%;
      height: auto;
      display: block;
    }

    .slider-caption {
      position: absolute;
      bottom: 0;
      width: 100%;
      background-color: rgba(0, 0, 0, 0.5); /* 半透明黑色背景 */
      color: white;
      padding: 10px;
      text-align: center;
      transition: transform 0.3s ease, opacity 0.3s ease; /* 添加过渡效果 */
      transform: translateY(100%); /* 初始状态下移出视图 */
      opacity: 0; /* 初始状态下透明 */
    }

    &:hover .slider-caption {
      transform: translateY(0); /* 悬停时移动回视图 */
      opacity: 1; /* 悬停时显示 */
    }

    .slider-title {
      font-size: 1.2rem;
      margin-bottom: 0; /* 标题的下边距为0 */
    }

    .slider-conference {
      font-size: 1rem;
      margin-top: 5px; /* 会议名称和标题之间添加间距 */
    }
  }

  .prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    color: white;
    font-weight: bold;
    font-size: 24px;
    user-select: none;
    background-color: rgba(0, 0, 0, 0.5); /* 半透明背景 */
    border-radius: 0 3px 3px 0;
    z-index: 10;

    &:hover {
      background-color: rgba(0, 0, 0, 0.8);
    }
  }

  .prev {
    left: 0;
    border-radius: 3px 0 0 3px;
  }

  .next {
    right: 0;
    border-radius: 0 3px 3px 0;
  }
}





